.c-social-buttons {
  margin-top: 2em;
  display: flex;
  gap: 2px;
  align-items: center;
  font-size: 1.2em;
  gap: 2em;
  list-style: none;
  a,
  .c-social-button {
    display: inline-flex;
    align-items: center;
  }
}

.c-header__contact {
  margin-top: 2em;
}
